.social-block {
  position: relative;

  &:hover {
    .social-block__list {
      opacity: 1;
      pointer-events: auto;
    }
  }
}

.social-block__toggler {
  @include btn-reset;
  position: relative;
  display: block;
  width: 4rem;
  height: 4rem;
  color: var(--socialBlockTogglerColor);
  border-radius: 50%;
  visibility: hidden;

  @media (max-width: $tablet) and (orientation: portrait) {
    display: none;
  }

  @media (max-width: $mobile) and (orientation: landscape) {
    display: none;
  }

  &::before {
    @include center;
    content: "";
    width: 100%;
    height: 100%;
    border-radius: 50%;
    background-color: $c-light;
    border: 2px solid $c-light;
    z-index: -1;
    transform: translate3d(-50%, -50%, 0) scale3d(0, 0, 0);
    transition: all $animation-time-2 ease,
      transform $animation-time-2 cubic-bezier(0.25, 0.1, 0.39, 1.55) 0.4s;
  }

  svg {
    @include center;
    display: block;
    width: 2.3rem;
    height: 2.2rem;
    opacity: 0;
    transition: opacity 0s ease 0.35s;
  }

  .social-block--active & {
    color: $c-light;
    transition: color $animation-time-2 ease;

    &::before {
      background-color: var(--menuLinkColor);
      border: 2px solid var(--menuLinkColor);
      width: 100%;
      height: 100%;
    }

    &:hover,
    &:focus:hover {
      color: var(--menuLinkColor);
    }

    &:focus,
    &:active:hover {
      color: $c-light;
    }
  }

  &:hover {
    color: var(--menuLinkColor);

    &::before {
      background-color: $c-light;
      border: 2px solid $c-light;
      width: 112.5%;
      height: 112.5%;
    }
  }

  &:focus {
    color: var(--menuLinkColor);
    outline: none;

    &::before {
      border-color: var(--menuLinkColor);
    }

    & + .social-block__list {
      opacity: 1;
      pointer-events: auto;
    }
  }

  &:active,
  &:active:focus {
    color: $c-light;

    &::before {
      width: 100%;
      height: 100%;
      background-color: var(--menuLinkColor);
      border-color: var(--menuLinkColor);
    }
  }
}

.social-block__list {
  @include list-reset;
  position: absolute;
  top: 100%;
  right: 0;
  margin-top: 1.5rem;
  opacity: 0;
  pointer-events: none;

  @media (min-width: $stop-scaling) {
    margin-top: 15px;
  }

  @media (max-width: $tablet) and (orientation: portrait) {
    position: static;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 0;
    opacity: 1;
    pointer-events: auto;
  }

  @media (max-width: $mobile) and (orientation: landscape) {
    position: static;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 0;
    opacity: 1;
    pointer-events: auto;
  }

  &::before {
    content: "";
    position: absolute;
    bottom: 100%;
    left: 0;
    right: 0;
    height: 1.5rem;

    @media (min-width: $stop-scaling) {
      height: 15px;
    }
  }

  &:hover {
    opacity: 1;
    pointer-events: auto;
  }

  &:focus-within {
    opacity: 1;
    pointer-events: auto;

    .social-block__link {
      opacity: inherit;
    }
  }

  .social-block--active & {
    opacity: 1;
    pointer-events: auto;

    .social-block__link {
      opacity: inherit;
      transform: translateY(0);
    }
  }

  li {
    flex: none;
    margin-bottom: 0.5rem;

    @media (min-width: $stop-scaling) {
      margin-bottom: 5px;
    }

    @media (max-width: $tablet) and (orientation: portrait) {
      margin: 0 1.25rem;
    }

    @media (max-width: $mobile) and (orientation: landscape) {
      margin: 0 1.25rem;
    }
  }

  @for $i from 1 through 3 {
    li:nth-of-type(#{$i}) {
      .social-block__link {
        transition: transform $animation-time-3 ease,
          opacity $animation-time-3 ease,
          color 0.2s ease;
        transition-delay: $animation-time-1 * $i - $animation-time-1;
      }
    }
  }
}

.social-block__link {
  position: relative;
  display: block;
  width: 4rem;
  height: 4rem;
  color: $c-light;
  border: 2px solid transparent;
  border-radius: 50%;
  opacity: 0;
  transform: translateY(0.7rem);

  @media (min-width: $stop-scaling) {
    width: 40px;
    height: 40px;
  }

  @media (max-width: $tablet) and (orientation: portrait) {
    color: $c-light;
    background-color: $c-dark;
    opacity: inherit;
    transform: none;
  }

  @media (max-width: $mobile) and (orientation: landscape) {
    color: $c-light;
    background-color: $c-dark;
    opacity: inherit;
    transform: none;
  }

  &:hover {
    color: var(--menuLinkColor);

    @media (max-width: $tablet) and (orientation: portrait) {
      color: $c-purple;
    }

    @media (max-width: $mobile) and (orientation: landscape) {
      color: $c-purple;
    }
  }

  &:focus {
    color: var(--menuLinkColor);
    background-color: $c-light;
    border-color: var(--menuLinkColor);
    outline: none;

    @media (max-width: $tablet) and (orientation: portrait) {
      color: $c-purple;
      border-color: $c-purple;
    }

    @media (max-width: $mobile) and (orientation: landscape) {
      color: $c-purple;
      border-color: $c-purple;
    }
  }

  &:active,
  &:active:focus {
    color: var(--menuLinkColor);
    background-color: $c-light;
    border-color: $c-light;

    @media (max-width: $tablet) and (orientation: portrait) {
      color: $c-purple;
      background-color: $c-light;
      border-color: $c-light;
    }

    @media (max-width: $mobile) and (orientation: landscape) {
      color: $c-purple;
      background-color: $c-light;
      border-color: $c-light;
    }
  }

  svg {
    @include center;
  }

  &--fb {
    svg {
      width: 0.8rem;
      height: 1.7rem;

      @media (min-width: $stop-scaling) {
        width: 8px;
        height: 17px;
      }
    }
  }

  &--insta {
    svg {
      width: 1.5rem;
      height: 1.5rem;

      @media (min-width: $stop-scaling) {
        width: 15px;
        height: 15px;
      }
    }
  }

  &--vk {
    svg {
      width: 1.7rem;
      height: 1rem;

      @media (min-width: $stop-scaling) {
        width: 10px;
        height: 10px;
      }
    }
  }
}
